
.add-plant-popup .popup-overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, 0.4);
    cursor: pointer;

}

.add-plant-popup .popup_inner {
    position: absolute;
    left: 30%;
    right: 30%;
    top: 20%;
    height: 75%;
    z-index: 2;

    margin: auto;
    background: white;
    border-radius: 5px;
    overflow-y: scroll;
    padding: 16px;

}

.popup_inner .popup_inner-border{
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.popup_inner .ap-title {
    -ms-flex: 0.5 1;
        flex: 0.5 1;
    width: 100%;

    text-align: center;
    font-size: 18px;
    color: rgba(0,0,0,0.6);
}
 .popup_inner .ap-title .exit{
    float: right;
    font-size: 14px;
}
 .popup_inner .ap-title .exit:hover{
    color: #d62e2e;
}

 .popup_inner .ap-photo{
    -ms-flex: 4 1;
        flex: 4 1;
    margin: 5% 25% 0 25%;
