    padding-left: 5px;
    border: 1px rgba(0,0,0,0.2) solid;
}
 .popup_inner .ap-date-added{
    -ms-flex: 1 1;
        flex: 1 1;
    margin-top: 10px;
    border-radius: 5px;
    padding-left: 5px;
    border: 1px rgba(0,0,0,0.2) solid;
}
 .popup_inner .ap-submit{
    -ms-flex: 0.75 1;
